.Btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 45px;
    height: 45px;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition-duration: .3s;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.199);
    background-color: rgb(163, 142, 255);
    padding: 0 10px;
  }
  
  /* plus sign */
  .sign {
    width: 100%;
    /* transition-duration: .3s; */
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .sign svg {
    width: 17px;
    height: 17px;
  }
  
  .sign svg path {
    fill: white;
  }
  /* text */
  .text {
    /* margin-left: 3px; */
    width: 0;
    overflow: hidden;
    opacity: 0;
    color: white;
    font-size: 1.2em;
    font-weight: 600;
    transition: all 0.3s ease;
  }
  /* hover effect on button width */
  .Btn:hover {
    /* width: 130px; */
    width:fit-content;
    border-radius: 40px;
    transition-duration: .3s;
  }
  
  .Btn:hover .sign {
    width: 30%;
    transition-duration: .3s;
    padding-left: 20px;
  }
  /* hover effect button's text */
  .Btn:hover .text {
    opacity: 1;
    width: auto;
    transition-duration: .3s;
    padding-right: 10px;
  }
  /* button click effect*/
  .Btn:active {
    transform: translate(2px ,2px);
  }